.ToDoSearch{
    background: #f9FbFc;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1e1e1f;
    box-shadow: 0px 5px 50px rgba(32,35,41,0,25);

}

.ToDoSearch::placeholder{
    color: #a5a5a5;
    font-family: 'Montserrat';
    font-weight: 400;
}
.ToDoSearch:focus{
    outline-color: #61dafa;
}